 /* ---------------------------------------------------
    CONTENT STYLE
 ----------------------------------------------------- */
 
 .content {
    width: 100%;
    padding: 20px;
    min-height: 100vh;
    background-color: #f1f1f1;
    /* background-color: #f8f9fa; */
    transition: all 0.3s;
 }
 
 h4{
    font-size:1.0rem;
 }
 .heading{display: inline-flex;width: 30%;}
 
 .heading h4{
    font-size: 18px;
    padding-top: 10px;
    margin-left: 0%;
    font-weight: 550;
    color: #585b60;
}

.contentcolor{background-color: #f8f9fa !important;}

.nav-button1{ 
   background-color: #efe9dd;
   font-weight: 550;
   color: #ff792b;
   letter-spacing: .5;
   padding: 4px 16px;
   font-size: 15px;
   border: 2px solid #f5d6bd;
   /* border-bottom: 4px solid #083976; */
   border-radius: 10px;
   font-weight: 400;
   margin-left: 10%;}

