 .nav-button{
    /* background-image: linear-gradient(to right, #2875c6 , #3fa5de); */
    background-color: #efe9dd;
    font-weight: 550;
    color: #ff792b;
    letter-spacing: .5;
    padding: 9px 12px;
    font-size: 15px;
    border: 2px solid #f5d6bd;
    /* border-bottom: 4px solid #083976; */
    border-radius: 10px;
    font-weight: 400;}
 .menu-list a{
    font-size: 14px;
    text-decoration: underline;
 }
 .nav-button:focus{outline: none;}

 /* ---------------------------------------------------
    Card css start here
 ----------------------------------------------------- */
 .row1{margin-top: 3%;} 
.dashboardcard{width: 345px !important;margin: auto !important;cursor: pointer;border-radius: 10px !important;box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;border: 1px solid #cccccc;}
.cardtitle{border-radius: 10px !important;background-color: #fff !important; padding: 0.28rem 0.28rem !important;border-bottom: 1px solid rgb(0 0 0 / 7%) !important;}
.card-body {padding: 0.5rem !important;}
.cardtext{padding: 3%;color: #585b60; font-weight: 500;text-align: center;}
.titleDashboard{font-size: 17px !important;color: #ff792b !important;text-align: left;padding-bottom: 2%;padding-top: 1%;}
.title2{font-size: 13px;text-decoration: underline;color: #5bb9e6;text-align: right;padding-bottom: 2%;padding-top: 1%;}
.title3{font-size: 14px;color: #41cd6a;text-align: left;padding-right: 0px !important;padding-left: 0px !important;} 
.title4{font-size: 14px; color: #6bdd8c;text-align: right;padding-right: 0px !important;padding-left: 0px !important;}
.addcolor{color: #f9c863 !important;font-size: 14px;text-align: right;}
.cardfooter{background-color: #fff !important;}
