.thead{background-color: #6f8293 !important;color: #fff;font-family: 'Roboto', Helvetica;font-size:14px;}
.table thead th {text-align: center !important;}
tbody{text-align: center !important;font-size:13.5px;}
thead tr:nth-child(1) th{background: #6f8293;position: sticky;top: 0; z-index: 10;}
th{font-size: 14px;}
.table th, .table td{padding: 0.3rem !important;}
.icon-inline{display: inline-flex; width: 100%;}
img{cursor: pointer;}
.edit{margin-left: 14%;}
.delete{margin-left: 10%;}
.closetb{text-align: right; margin-right: 0px !important;margin-left: 0px !important;}

/* top-left border-radius */
table tr:first-child th:first-child {
    border-top-left-radius: 6px;
  }
  
  /* top-right border-radius */
  table tr:first-child th:last-child {
    border-top-right-radius: 6px !important;
  }
  
  /* bottom-left border-radius */
  table tr:last-child td:first-child {
    border-bottom-left-radius: 6px !important;
  }
  
  /* bottom-right border-radius */
  table tr:last-child td:last-child {
    border-bottom-right-radius: 6px !important;
  }
