@import url('https://fonts.googleapis.com/css?family=Raleway:400,700');

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;	
	font-family: Raleway, sans-serif;
}

.bg-color {
	background: linear-gradient(90deg, #C7C5F4, #1851af);
	/* background: url('../images/banner1.jpg'); */
}

.container {
	display: flex;
	align-items: center;
	justify-content: center;
	min-height: 100vh;
}

.screen {		
	/* background: linear-gradient(90deg, #5D54A4, #7C78B8);		 */
	background: linear-gradient(90deg, #1851af, #90a6cb);
	position: relative;	
	/* height: 600px; */
    height: 500px;
	width: 360px;	
	box-shadow: 0px 0px 24px #5C5696;
}

.screen__content {
	z-index: 1;
	position: relative;	
	height: 100%;
}

.screen__background {		
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 0;
	-webkit-clip-path: inset(0 0 0 0);
	clip-path: inset(0 0 0 0);	
}

.screen__background__shape {
	transform: rotate(45deg);
	position: absolute;
}

.screen__background__shape1 {
	/* height: 520px; */
	height: 490px;
	width: 520px;
	background: #FFF;	
	top: -50px;
	right: 120px;	
	border-radius: 0 72px 0 0;
    /* border: 2px solid #4d4a7c; */
	border: 3px solid #f79537;
}

.screen__background__shape2 {
	height: 220px;
	width: 220px;
	/* background: #6C63AC;	 */
	background: #6c63ac8f;
	top: -172px;
	right: 0;	
	border-radius: 32px;
}

.screen__background__shape3 {
	height: 540px;
	width: 190px;
	background: linear-gradient(270deg, #5D54A4, #6A679E);
	top: -24px;
	right: 0;	
	border-radius: 32px;
}

