.btn-two{width: 100%;}

.log-btn{display: inline-flex;}

.login__submit{background: #fff;
	margin-right: 14px;
    font-size: 14px;
    margin-top: 30px;
    padding: 13px 48px;
    border-radius: 26px;
    border: 1px solid #D4D3E8;
    text-transform: uppercase;
    font-weight: 700;
    display: flex;
    align-items: center;
    /* width: 100%; */
    color: #4C489D;
    box-shadow: 0px 2px 2px #5c5696;
    cursor: pointer;
    transition: .2s;}

.login__cancel{background: #fff;
		font-size: 14px;
		margin-top: 30px;
		padding: 13px 48px;
		border-radius: 26px;
		border: 1px solid #D4D3E8;
		text-transform: uppercase;
		font-weight: 700;
		display: flex;
		align-items: center;
		/* width: 100%; */
		color: #4C489D;
		box-shadow: 0px 2px 2px #5c5696;
		cursor: pointer;
		transition: .2s;}	

.login__submit:active,
.login__submit:focus,
.login__submit:hover {
	border-color: #6A679E;
	outline: none;
}

.login__cancel:active,
.login__cancel:focus,
.login__cancel:hover {
	border-color: #6A679E;
	outline: none;
}

