 .content {
   width: 100%;
   padding: 20px;
   min-height: 100vh;
   background-color: #f8f9fa;
   transition: all 0.3s;
}

h4{
   font-size:1.0rem;
}
.heading{display: inline-flex;width: 30%;}

.heading h4{
   font-size: 18px;
   padding-top: 10px;
   margin-left: 0%;
   font-weight: 550;
   color: #585b60;
}
