 /* ---------------------------------------------------
    CONTENT STYLE
 ----------------------------------------------------- */
 
 .content {
    width: 100%;
    padding: 20px;
    min-height: 100vh;
    background-color: #f8f9fa;
    transition: all 0.3s;
 }
 .filter-row{
   margin-bottom: 2%;
 }
 .filter-input{
   width: 90%;
 }
 h4{
    font-size:1.0rem;
 }
 .heading{display: inline-flex;width: 30%;}
 
 .heading h4{
    font-size: 18px;
    padding-top: 10px;
    margin-left: 0%;
    font-weight: 550;
    color: #585b60;
}

#filter-modal{ background-color: #efe9dd;height: 37px;
   font-weight: 550;
   color: #ff792b;
   letter-spacing: .5;
   padding: 4px 16px;
   font-size: 15px;
   border: 2px solid #f5d6bd;
   /* border-bottom: 4px solid #083976; */
   border-radius: 10px;
   font-weight: 400;display: inline-flex;}

.add-icon{text-align: right; margin-bottom: 4px;}
.btn-filter{font-weight: 550;}  

.nav-button-run{ background-color: #efe9dd;height: 37px;
   font-weight: 550;
   color: #ff792b;
