 .nav-button{
    /* background-image: linear-gradient(to right, #2875c6 , #3fa5de); */
    background-color: #efe9dd;
    font-weight: 550;
    color: #ff792b;
    letter-spacing: .5;
    padding: 9px 12px;
    font-size: 15px;
    border: 2px solid #f5d6bd;
    /* border-bottom: 4px solid #083976; */
    border-radius: 10px;
    font-weight: 400;}
 .menu-list a{
    font-size: 14px;
    text-decoration: underline;
 }
 .nav-button:focus{outline: none;}
 
 
 /* ---------------------------------------------------
   Tab css start here
 ----------------------------------------------------- */
 .contentcolor{background-color: #f8f9fa !important;}
.row1{margin-top: 2%;}
.row2{margin-top: 4%;}
.nav-tabs{background-color: #fff;font-size: 15px;margin-left: 2%; margin-right: 4%; border-left: 1px solid #f5c8a4;
   border-radius: 7px;
   border-right: 1px solid #f5c8a4;
   border-top: 1px solid #f5c8a4;
   border-bottom: 1px solid transparent;}
.nav-tabs li{border-bottom: 1px solid #dee2e6;}
.tab-content{background-color: #fff;margin-left: 2%; margin-right: 4%;padding: 2%;border: 1px solid #f5c8a4;
border-bottom-left-radius: 15px;border-bottom-right-radius: 15px;box-shadow: rgba(0, 0, 0, 0.2) 0px 18px 50px -10px;}
.nav-tabs .nav-link{background-color: #fff;}
.nav-tabs .nav-link:focus, .nav-tabs .nav-link:hover{background-color: #fff;}
.nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link.active{font-weight: 550;border-right: 2px solid #f5c8a4;border-top: 2px solid #f5c8a4;border-bottom: 2px solid transparent;border-left: 2px solid #f5c8a4;}
.resourcetab h5{color: #4c4b81;font-size: 19px;letter-spacing: 0px; margin-bottom: 2%; margin-top: 5px;}
.resourcetab p{color: #4c6081;margin-bottom: 2%;}

.title2{text-align: right;}
.title2 img{cursor: pointer;vertical-align: baseline;}
.title1{color: #4c6081 !important;font-size: 18px !important;font-weight: 550 !important;}
