/* ---------------------------------------------------
    CONTENT STYLE
 ----------------------------------------------------- */
 
 .content {
    width: 100%;
    padding: 20px;
    min-height: 100vh;
    background-color: #f8f9fa;
    transition: all 0.3s;
 }
 
 h4{
    font-size:1.0rem;
 }
 .heading{display: inline-flex;width: 30%;}
 
 .heading h4{
    font-size: 18px;
    padding-top: 10px;
    margin-left: 0%;
    font-weight: 550;
    color: #585b60;
}

.contentcolor{background-color: #f8f9fa !important;}

.nav-button1{ background-color: #efe9dd;
   font-weight: 550;
   color: #ff792b;
   letter-spacing: .5;
   padding: 4px 16px;
   font-size: 15px;
   border: 2px solid #f5d6bd;
   /* border-bottom: 4px solid #083976; */
   border-radius: 10px;
   font-weight: 400;
   margin-left: 10%;}

.nav-button3{ background-color: #efe9dd;
      font-weight: 550;
      color: #ff792b;
      letter-spacing: .5;
      padding: 4px 16px;
      font-size: 15px;
      border: 2px solid #f5d6bd;
      /* border-bottom: 4px solid #083976; */
      border-radius: 10px;
      font-weight: 400;
      margin-left: 2%;}

.nav-button2{ background-color: #efe9dd;
      font-weight: 550;
      color: #ff792b;
      letter-spacing: .5;
      padding: 4px 16px;
      font-size: 15px;
      border: 2px solid #f5d6bd;
      /* border-bottom: 4px solid #083976; */
      border-radius: 10px;
      font-weight: 400;}   
 
 .nav-button{
    /* background-image: linear-gradient(to right, #2875c6 , #3fa5de); */
    background-color: #efe9dd;
    font-weight: 550;
    color: #ff792b;
    letter-spacing: .5;
    padding: 9px 12px;
    font-size: 15px;
    border: 2px solid #f5d6bd;
    /* border-bottom: 4px solid #083976; */
    border-radius: 10px;
    font-weight: 400;}
 .menu-list a{
    font-size: 14px;
    text-decoration: underline;
 }
 .nav-button:focus{outline: none;}

  /* ---------------------------------------------------
   Form css start here
 ----------------------------------------------------- */
.row1{margin-top: 2%;}
/* .formedit{margin-left: 4%;} */
.formedit{padding: 2% 5% 2% 5%;}
 .card-form{margin-left: 30px;border-radius: 14px !important;width: 900px !important;border-radius: 17px;text-align: left !important;box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;border: 1px solid #cccccc;}
.heading-icon{display: inline-flex;width: 100%;border-bottom: 1px solid #eeeeee;}
.heading-icon h4{width:100%;margin-left: 4%;margin-top: 1%;color: #0a1e3f;font-size: 18px;font-weight: 550; padding-bottom: 1%;}
.heading-icon img{width: 22px;height: 22px;vertical-align: text-bottom;margin-right: 1%;}
label{ font-size: 14px; font-weight: bold;color: #405576;margin-left: 16px;margin-top: 5%;}
input, select, textarea:focus{outline: none;}
.wdt{margin-left: 34% !important;width: 236px;}
.wdt2{margin-left: 8% !important;width: 236px;}
.wdt3{margin-left: 4%;width: 240px;}
/* .wd{margin-left: 26% !important;width: 200px;} */
input{background-color: transparent;border-radius: 6px;font-size: 13px !important;height: 34px;border: 1px solid #c5c5c5;padding-left: 6px;}
select{background-color: transparent;margin-left: 26% !important;border-radius: 6px;font-size: 13px !important; height: 34px;border: 1px solid #c5c5c5;padding-left: 6px;}
.row-bottom{ margin-bottom: 2%;}
.row-bottom-top{ margin-bottom: 2%;margin-top: 2%;}
.nodes{ margin-left: 22% !important;}
.channel{margin-left: 33% !important;}
.owner{margin-left: 55% !important;}
.member{margin-left:7% !important;}
.codefilealign{margin-left: 7% !important;border: 1px solid transparent; font-size: 12px !important;}
/* .network{margin-left: 47%;} */
.nav-save{background-color: #efe9dd;height: 38px;
   font-size: 15px !important;font-weight: bold;font-weight: 550;
   color: #ff792b;letter-spacing: .5;padding: 4px 18px;height: 38px;
   border: 2px solid #f5d6bd;border-radius: 10px;font-weight: 400;}

.nav-save:hover{ background-image: linear-gradient(to right, #ff9d0a , #ff7c28); color: #fff;}            
.sub-button:hover{ background-image: linear-gradient(to right, #ff9d0a , #ff7c28); color: #fff;}    
.nomargin{margin-right: 0px !important;margin-left: 0px !important;}
.btn-btm{ width: 100%;text-align: center;margin-top: 3%;margin-bottom: 3%;}
.breadcrumb{margin-left: 45px;padding: 0.7rem 0rem 0rem 1rem !important;width: 74%;border-radius: 9px;}
.breadcrumb ul li{font-size: 14px;letter-spacing: .5px;color: #0a1e3f;list-style: none !important;}
.textarea-wdt{background-color: transparent;margin-left: 8%;
   border-radius: 6px;
   font-size: 13px !important;
   height: 34px;
   border: 1px solid #c5c5c5;
   padding-left: 6px;}
.sub-button{background-color: #efe9dd;height: 38px;margin-left: 2%;
font-size: 15px !important;font-weight: bold;font-weight: 550;
color: #ff792b;letter-spacing: .5;padding: 4px 18px;height: 38px;
border: 2px solid #f5d6bd;border-radius: 10px;font-weight: 400;}  




