
.content {
  width: 100%;
  padding: 20px;
  min-height: 100vh;
  height: auto;
  background-color: #f8f9fa;
  transition: all 0.3s;
  
}

h4{
  font-size:1.0rem;
}
.heading{display: inline-flex;width: 30%;}

.heading h4{
  font-size: 18px;
  padding-top: 10px;
  margin-left: 0%;
  font-weight: 550;
  color: #585b60;
  margin-bottom: 07%;
}

.nav-button{
  /* background-image: linear-gradient(to right, #2875c6 , #3fa5de); */
  background-color: #efe9dd;
  font-weight: 550;
  color: #ff792b;
  letter-spacing: .5;
  padding: 9px 12px;
  font-size: 15px;
  border: 2px solid #f5d6bd;
  /* border-bottom: 4px solid #083976; */
  border-radius: 10px;
  font-weight: 400;}
.menu-list a{
  font-size: 14px;
  text-decoration: underline;
}
.nav-button:focus{outline: none;}

.bpp-properties{
  height:119vh;
  
}

.butt {
  /* background-image: linear-gradient(to right, #2875c6 , #3fa5de); */
  background-color: #efe9dd;
  font-weight: 550;
  color: #ff792b;
  letter-spacing: 0.5;
  padding: 9px 5px;
  font-size: 15px;
  border: 2px solid #f5d6bd;
  /* border-bottom: 4px solid #083976; */
  border-radius: 10px;
  font-weight: 400;
  float: center;
  margin:1px 40px 20px 40px;

}
.butt:hover{ background-color: #efe9dd;color: #ff792b;border: 2px solid #f5d6bd;}

svg{
  color: white;
  height: 100%;
}

.butt {
  outline: none;
}



